*{
    box-sizing: border-box;
   }
   body {
     margin: 20px;
     background: rgba(63, 94, 251, 0.434);
background: radial-gradient(circle, rgba(63, 94, 251, 0.683) 0%, rgba(252, 70, 106, 0.724) 100%);
     color: white;
     text-align: center;
     font-family: 'Poppins', sans-serif;
   }
   
   .iphoneCase1  {
     --border-radius-outer: 2rem;
     --outer-padding: 0.5rem;
     --case-color: hsla(0, 0%, 100%, 0);
     --border-radius-inner: calc( var(--border-radius-outer) - var(--outer-padding));
     box-shadow: 0px 0px 0px 2px rgb(0, 0, 0);
     background-color: var(--case-color);
     border-radius: var(--border-radius-outer);
     padding: var(--outer-padding);
     width: min(330px);
     aspect-ratio: 1/2;
     position: relative;
     margin-inline: auto;
    background-color: rgba(196, 200, 203, 0.553);
    height: 660px;
    overflow: none;
   }   
   .iphoneCase1::-webkit-scrollbar{
    display: none;
   }
   
   .island{
    width: 120px;
    height: 28px;
    /* border: 1px solid ; */
   margin-left: auto;
   margin-right: auto;
    background-color: black;
    background-image: url(../components/assets/img/img1.png);
    margin-bottom: 10px;
    background-size: cover;
    background-position: center;
    border-radius: 22px;
    background-size: cover;
   }
 
