.inputText{
  width: 200px;
  height: 40px;
  border-radius: 10px;
  border: none;
  text-align: center;
}

.searchContainer{
  width: 100%;
  display: flex;
  justify-content: center;
  margin-left: 10px;
}
.btnType{
  font-size: 25px;
  /* border: 1px solid; */
  margin-left: 10px;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.resultContainer{
  width: 100%;
  height: 550px;
  /* border: 1px solid; */
  /* margin-top: 30px; */
  overflow: scroll;
  margin-left: 7px;
}
.resultContainer::-webkit-scrollbar{
  display: none;
}
.card{
  transition: 200ms;

}
.modalWindow{
  width: 200px;
  height: 300px;
  border: 1px solid;
  background-color: black;
}